.preview-focal-point {
    width: 16px;
    height: 16px;
    border: 2px solid rgba(255,255,255,0.8);
    background: rgba(0,0,0,0.5);
    position: absolute;
    border-radius: 16px;
    cursor: pointer;
}
.preview-focal-point .inner {
    position: absolute;
    width: 0;
    height: 0;
    left: 6px;
    top: 6px;
    background: rgba(255,255,255,0.8);
    border: 2px solid rgba(255,255,255,0.8);
    border-radius: 4px;
}

